body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-size: 10px;
  background: url(/drum-kit/static/media/background.214658d2.jpg) no-repeat center center fixed;
  background-size: cover;
}

body {
  background: inherit;
}

body,html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.keys {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  min-height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.key {
  border: .4rem solid black;
  border-radius: .5rem;
  margin: 1rem;
  font-size: 1.5rem;
  padding: 1rem .5rem;
  transition: all .07s ease;
  width: 10rem;
  text-align: center;
  color: white;
  background: rgba(0,0,0,0.4);
  text-shadow: 0 0 .5rem black;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
}

.key-inlay {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.playing {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);

}

.kbd {
  display: block;
  font-size: 4rem;
}

.sound {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
}

.fall-off-screen {
    -webkit-animation: fall 3s;
            animation: fall 3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes fall {
  0% {
    /* transform: translateY(0vh); */
  }
  50% {
    opacity: 0.8;
  }
  100% {
    /* transform: translateY(50vh); */
    opacity: 0;
    top: 500px;
  }
}

@keyframes fall {
  0% {
    /* transform: translateY(0vh); */
  }
  50% {
    opacity: 0.8;
  }
  100% {
    /* transform: translateY(50vh); */
    opacity: 0;
    top: 500px;
  }
}

.behind {
  z-index: -1;
  position: relative;

}

